<template>
  <!-- 首页 -->
  <a-layout id="home-body">
    <a-layout-sider>
      <div class="logo">
        <img src="/logo.png" alt="" />
      </div>
      <a-menu
        :style="{ height: '100%', background: '#3B7FFF', color: '#fff' }"
        @select="scope"
      >
        <a-menu-item key="/productlist">
          <span>商品管理</span>
        </a-menu-item>
        <a-menu-item key="/management">
          <span>订单管理</span>
        </a-menu-item>
        <a-menu-item key="/userslist" disabled>
          <span>用户管理</span>
        </a-menu-item>
        <a-menu-item key="/essential">
          <span>合同管理</span>
        </a-menu-item>
        <a-menu-item key="/allocation">
          <span>配置管理</span>
        </a-menu-item>
        <a-menu-item key="/mine">
          <span>我的</span>
        </a-menu-item>
      </a-menu>
    </a-layout-sider>
    <a-layout>
      <a-layout-header id="home-header">
        <div class="home-conter">
          <div>
            <img src="/默认头像.png" alt="" />
          </div>
          <div>
            <span>{{ merdata.name }}</span>
            <span>账号:{{ merdata.phone }}</span>
          </div>
          <div>
            <img @click="tc" src="/退出.png" alt="" />
          </div>
        </div>
      </a-layout-header>
      <a-layout-content
        :style="{
          margin: '24px 16px',
          padding: '24px',
          background: '#fff',
          minHeight: '80vh',
        }"
      >
        <router-view></router-view>
      </a-layout-content>
    </a-layout>
  </a-layout>
</template>
<script>
export default {
  data() {
    return {
      collapsed: true,
      merdata: [],
    };
  },
  mounted() {
    this.item();
  },
  methods: {
    async item() {
      let str = {
        id: sessionStorage.getItem("id"),
      };
      let { data } = await this.$http.getmer(str);
      if (data.code == "0000") {
        this.merdata = data.data.mer;
      }
    },

    scope(item) {
      let key = item.key;
      /*   console.log(key); */
      this.$router.push(key);
    },
    async tc() {
      let str = {
        phone: this.merdata.phone,
        token: sessionStorage.getItem("token"),
      };
      let { data } = await this.$http.logout(str);
      if (data.code == "0000") {
        this.$message({
          showClose: true,
          message: "退出登录成功",
          type: "success",
        });
        sessionStorage.clear();
        this.$router.replace("/");
      }
    },
  },
};
</script>
